import axios from 'axios';

const API_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000';

const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

export interface FileDetails {
  name: string;
  desc: string;
}

export interface UserFiles {
  user_csv?: FileDetails;
  user_doc?: FileDetails;
}

export interface QuestionAnswer {
  question: string;
  answer: string;
}

export interface ToolInfo {
  name: string;
  description: string;
}

export interface AgentTeamSummary {
  team_name: string;
  design_pattern: string;
  description?: string;
}

// Create Agent Team Initial
export async function createAgentTeamInitial(
  description: string,
  industry: string,
  department: string,
  csvFile?: File,
  csvDesc?: string,
  docFile?: File,
  docDesc?: string
) {
  const formData = new FormData();
  formData.append('description', description);
  formData.append('industry', industry);
  formData.append('department', department);
  
  if (csvFile) {
    formData.append('csv_file', csvFile);
    formData.append('csv_desc', csvDesc || '');
  }
  
  if (docFile) {
    formData.append('doc_file', docFile);
    formData.append('doc_desc', docDesc || '');
  }
  
  const response = await api.post('/createAgentTeamInitial', formData, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  });
  
  return response.data;
}

// Create Agent Team PRD
export async function createAgentTeamPRD(
  qaPrompt: QuestionAnswer[],
  description: string,
  files: UserFiles,
  availableTools: ToolInfo[]
) {
  const payload = {
    qa_pairs: qaPrompt,
    description,
    files,
    available_tools: availableTools,
  };
  
  const response = await api.post('/createAgentTeamPRD', payload);
  return response.data;
}

// List Agent Teams
export async function listAgentTeams() {
  const response = await api.get('/listAgentTeams');
  return response.data;
}

// Inference Agent Team
export async function inferenceAgentTeam(agentTeamName: string, userQuery: string) {
  const payload = {
    agent_team_name: agentTeamName,
    user_query: userQuery,
  };
  
  const response = await api.post('/inferenceAgentTeam', payload);
  return response.data;
}

// List Uploaded Files
export async function listUploadedFiles() {
  const response = await api.get('/listUploadedFiles');
  return response.data;
}

export default api; 